import React, { useRef } from 'react';

export const WebcamCapture = () => {
    const videoRef = useRef<HTMLVideoElement>(null);
    // 2-i MediaDevicesオブジェクトを用意する
    const media = navigator.mediaDevices;
    // 2-ii Webカメラを使っていいかユーザに聞く
    const constraints = { audio: false, video: true };
    media.getUserMedia(constraints)
        // 2-iii 取得できた映像・音声データを処理する
        .then(function (stream) {
            if (videoRef.current) {
                videoRef.current.srcObject = stream;
            }
        });

    const handleCapture = () => {
        if (videoRef.current) {
            const canvas = document.createElement('canvas');
            const context = canvas.getContext('2d');

            // ビデオのサイズにキャンバスを合わせる
            canvas.width = videoRef.current.videoWidth;
            canvas.height = videoRef.current.videoHeight;

            // キャンバスにビデオのフレームを描画
            if (context) {
                context.drawImage(
                    videoRef.current,
                    0,
                    0,
                    videoRef.current.videoWidth,
                    videoRef.current.videoHeight
                );

                // // キャンバスのデータを画像として保存
                // const image = canvas.toDataURL('image/png');
                // const link = document.createElement('a');
                // link.href = image;
                // link.download = 'captured_image.png';
                // link.click();

                // canvas画像をLocalStorageに保存
                window.localStorage["Hackathon_sake"] = canvas.toDataURL();
                // document.getElementById().src = window.localStorage["Hackathon_sake"];
            }
        }
    };

    return (
        <div>
            <h2>Webカメラで撮影</h2>
            <video ref={videoRef} width={640} height={480} autoPlay></video>
            <button onClick={handleCapture}>撮影</button>
        </div>
    );
};
