import { Radar } from 'react-chartjs-2';
import {
    Chart as ChartJS,
    RadialLinearScale,
    PointElement,
    LineElement,
    Filler,
    Tooltip,
    Legend,
} from 'chart.js';

ChartJS.register(
    RadialLinearScale,
    PointElement,
    LineElement,
    Filler,
    Tooltip,
    Legend
);

export const RadarChart = () => {
    // データの設定
    const data = {
        labels: ['香り', '味', '酸味', '甘さ', 'コク'],
        datasets: [
            {
                label: '日本酒の特徴',
                data: [4, 3, 5, 2, 4], // 各特徴の評価値（1から5まで）
                backgroundColor: 'rgba(75, 192, 192, 0.2)', // チャートの背景色
                borderColor: 'rgba(75, 192, 192, 1)', // チャートの枠線の色
                borderWidth: 2, // チャートの枠線の太さ
            },
        ],
    };

    // オプションの設定
    const options = {
        scale: {
            angleLines: {
                display: true, // 軸の目盛りの線を表示する
            },
            ticks: {
                beginAtZero: true, // 目盛りの最小値を0にする
                min: 0, // 目盛りの最小値
                max: 5, // 目盛りの最大値
                stepSize: 1, // 目盛りの刻み幅
                fontFamily: 'MS Gothic', // 目盛りのフォント（日本語）
            },
        },
        legend: {
            labels: {
                fontFamily: 'Arial', // 凡例のフォント（英数字）
            },
        },
    };

    return (
        <div>
            <h2>日本酒の特徴分析</h2>
            <Radar data={data} />
        </div>
    );
};
